import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from "react-redux";
import storeFactory from "./store";
import App from './App';

const store = storeFactory();

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

